import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { withRouter } from "react-router";
// import { models } from 'mongoose';
import './Nav.css';
import Auth from "../utils/Auth";
const auth = new Auth();

class Nav extends Component {

    state = {
        user: null
    }

    handleLogout = (e)=> {
        e.preventDefault();
        var fixProps = this.props;
        auth.logout()
            .then(()=> {
                this.setState({user: null}, ()=> {
                    fixProps.history.push("/login");
                })
                
            })
            .catch((error)=> {
                this.setState({error: error.message})
            })
    }

    componentDidMount() {
        this.setState({
            user: auth.getUser()
        })
    }

    render() {
        return (
            <nav>
                <Link to="#" onClick={this.props.history.goBack} className="white-text"><h5>back</h5></Link>
                <Link to="/"><h3>Home</h3></Link>
                <Link to="/login" className="white-text" onClick={this.handleLogout}><h5>Logout</h5></Link>
            </nav>
        )
    }
}

export default withRouter(Nav)