import React, { Component } from "react";
import axios from "axios";
import qs from "querystring";
import "./Editpage.css";

export default class Editpage extends Component {
  constructor(props) {
    super(props);
    this.formRef = React.createRef();
    this.domain = process.env.REACT_APP_API;
    this.state = {
      name: "",
      phoneNumber: "",
      date: "",
      rental: null,
      meal: null,
      amount: "",
      notes: ""
    };

    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.deleteOrder = this.deleteOrder.bind(this);
  }

  getOrders = id => {
    let found = this.props.orders.find(order => {
      return order._id === id;
    });
    return found;
  };

  componentDidMount() {
    const { params } = this.props.match;
    const foundOrder = this.getOrders(params.id);
    this.setState({
      name: foundOrder.name,
      phoneNumber: foundOrder.phoneNumber,
      date: foundOrder.date,
      rental: foundOrder.rental,
      meal: foundOrder.meal,
      amount: foundOrder.amount,
      notes: foundOrder.notes
    });
  }

  handleFormChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleFormSubmit = e => {
    e.preventDefault();
    let { name, phoneNumber, date, rental, meal, amount, notes } = this.state;

    let id = this.props.match.params.id;
    axios({
      method: "POST",
      url: "/edit",
      baseURL: this.domain,
      headers: { "content-type": "application/x-www-form-urlencoded" },
      data: qs.stringify({
        id,
        name,
        phoneNumber,
        date,
        rental,
        meal,
        amount,
        notes
      })
    })
      .then(response => {
        this.props.changeState(response.data);
        console.log("The order has been edited");
        this.props.history.push("/admin");
      })
      .catch(error => {
        console.log(error);
      });
  };

    deleteOrder = e => {
        e.preventDefault();
        let id = this.props.match.params.id;
        axios({
            method: "post",
            url: "/delete",
            baseURL: this.domain,
            headers: { "content-type": "application/x-www-form-urlencoded" },
            data: qs.stringify({id}),
            withCredentials: true
        })
        .then(response => {
            this.props.changeState(response.data)
            console.log("the order has been deleted");
            this.props.history.push("/admin");
        })
        .catch(error => {
            console.log("this error: " + error);
        });
    }


  render() {
    return (
      <div className="formDiv">
        <div className="flex-container-admin">
          <div className="thisBtnDiv">
            <button
              onClick={this.deleteOrder}
              className="waves-effect waves-light btn red thisBtn">
              Delete bestelling
            </button>
          </div>
        </div>
        <h3>Bewerk bestelling</h3>
        <form className="col s12" onSubmit={this.handleFormSubmit}>
          <div className=" col s6">
            <label>Naam: </label>
            <input
              type="text"
              name="name"
              value={this.state.name}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Tel.: </label>
            <input
              type="text"
              name="phoneNumber"
              value={this.state.phoneNumber}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Datum: </label>
            <input
              type="text"
              name="date"
              value={this.state.date}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Verhuur: </label>
            <input
              type="text"
              name="rental"
              value={this.state.rental}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Buffet keuze: </label>
            <input
              type="text"
              name="meal"
              value={this.state.meal}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Aantal personen: </label>
            <input
              type="number"
              name="amount"
              value={this.state.amount}
              onChange={this.handleFormChange}
            />
          </div>
          <div className=" col s6">
            <label>Notes: </label>
            <input
              type="text"
              name="notes"
              value={this.state.notes}
              onChange={this.handleFormChange}
            />
          </div>
          <button
            className="waves-effect waves-light btn"
            type="submit"
            value="submit"
          >
            Opslaan
          </button>
        </form>
      </div>
    );
  }
}
