import React, { Component } from "react";
import {Link} from "react-router-dom";
import WeekScheduleItem from "../components/WeekScheduleItem";
import axios from "axios";
import "./kitchen.css";
import KitchenDetail from "../components/KitchenDetail";
// import ProtectedRoute from "../pages/ProtectedRoute";
import { Route } from "react-router-dom";

export default class Kitchen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      days: "",
      orders: ""
    };
    this.changeDateState = this.changeDateState.bind(this);
  }

  dateLoop() {
    var i;
    var today = new Date();
    var addDate = new Date();
    var daysArr = [];

    for (i = 0; i < 7; i++) {
      daysArr.push(addDate.setDate(today.getDate()+i));
    }
    return daysArr;
  }

  addDates(){
    var i;
    var lastDay = new Date(this.state.days[(this.state.days.length-1)][6]);
    var addDate = new Date();
    var daysArr = [];

    for (i = 0; i < 7; i++) {
      daysArr.push(addDate.setDate(lastDay.getDate()+i));
    }
    return this.daysArr
  }

  changeDateState(){
    this.setState({days: [...this.state.days, this.addDates()] }); 
  }

  componentDidMount() {
    axios
      .get("http://localhost:3000/orders")
      .then(response => {
        this.setState({
          days: [...this.state.days, this.dateLoop()],
          orders: response.data
        });
      })
      .catch(err => {
        console.log(err);
      });
  }

  render() {
    return (
      <div className="kitchenDiv">
        <h3>Keuken</h3>
        <div className="flex-container-row">
          <div className="ScheduleList">
            {this.state.days &&
              this.state.days[0].map(thisDay => {
                return (
                  <Link to={`/keuken/${thisDay}`}> <WeekScheduleItem thisDay={thisDay} key={thisDay}/></Link>
                );
              })}
              <button onClick={this.changeDateState} className="waves-effect waves-light btn">Show more</button>
          </div>
          <div className="theKitchenDetailDiv">            <Route path="/keuken/:id" component={KitchenDetail} /></div>
        </div>
      </div>
    );
  }
}
