import React, { Component } from "react";
import { Link } from "react-router-dom";
// import axios from 'axios';
import "./Home.css";

export default class Home extends Component {


  render() {
    return (
      <div className="homepageDiv">
        <div className="row">
          <Link to="/nieuw">
            <div className="col s12 m6">
              <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <span className="card-title">Nieuwe bestelling</span>
                  <p>
                    Neem een nieuwe bestelling op en plaats deze bestelling in de database.
                  </p>
                </div>
              </div>
            </div>
          </Link>
          <Link to="/verhuur">
            <div className="col s12 m6">
              <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <span className="card-title">Verhuur</span>
                  <p>
                    Bekijk hier welke artikelen aankomende tijd verhuurd worden.
                  </p>
                </div>
              </div>
            </div>
          </Link>
          <Link to="/keuken">
            <div className="col s12 m6">
              <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <span className="card-title">Keuken</span>
                  <p>
                    Bekijk hier welke maaltijden er gemaakt moeten worden de
                    komende tijd.
                  </p>
                </div>
              </div>
            </div>
          </Link>
          <Link to="/admin">
            <div className="col s12 m6">
              <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                  <span className="card-title">Administratie</span>
                  <p>Alle informatie over de bestellingen kun je op deze pagina vinden.</p>
                </div>
              </div>
            </div>
          </Link>
        </div>
      </div>
    );
  }
}
