import React, { Component } from "react";

export default class RentalListItem extends Component {
  render() {
    return (
      <div>
        <ul className="collection">
          <li className="collection-item avatar rentalListItem">
            <span className="title">
              <strong>Naam: </strong>
              {this.props.thisOrder.name}
            </span>
            <p>
              <strong>Tel.: </strong>
              {this.props.thisOrder.phoneNumber} <br />
              <strong>Datum: </strong>
              {new Date(this.props.thisOrder.date).getDate() +
                "/" +
                (new Date(this.props.thisOrder.date).getMonth() +1) +
                "/" +
                new Date(this.props.thisOrder.date).getFullYear()}
            </p>
            <p>
              <strong>Verhuur: </strong>
              <ul>
                {this.props.thisOrder.rental.map(thisRental => {
                return <li>{thisRental}</li>
                })}
              </ul>
            </p>
            <p>
              <strong>Notes: </strong>
              {this.props.thisOrder.notes}
            </p>
          </li>
        </ul>
      </div>
    );
  }
}
