import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import axios from "axios";
import Home from "./pages/Home";
import Nav from "./components/Nav";
import NewOrder from "./pages/NewOrder";
import Verhuur from "./pages/Rental";
import Keuken from "./pages/Kitchen";
import Admin from "./pages/Admin";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import Detailpage from "./components/Detailpage";
import Editpage from "./pages/Editpage";
import ProtectedRoute from "./pages/ProtectedRoute";
import "./App.css";
// import { stringify } from "querystring";

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      orders: []
    };
    this.changeState = this.changeState.bind(this);
  }

  componentDidMount() {
    axios
      .get("http://localhost:3000/orders")
      .then(response => {
        this.setState({
          orders: response.data
        });
      })
      .catch(err => {
        console.log(err);
      });
  }

  changeState(newData) {
    this.setState({
      orders: newData
    })
  }

  render() {
    return (
      <div>
        <Nav />
        <div className="body-container">
          <Switch>
            <Route path="/login" component={Login} />
            <Route path="/signup" component={Signup} />
            <ProtectedRoute redirectUrl="/login" component={NewOrder} changeState={this.changeState} path="/nieuw" />
            <ProtectedRoute redirectUrl="/login" component={Verhuur} path="/verhuur" orders={this.state.orders}/>
            <ProtectedRoute redirectUrl="/login" component={Keuken} path="/keuken" orders={this.state.orders}/>
            <ProtectedRoute exact redirectUrl="/login" path="/admin" component={Admin} orders={this.state.orders} />
            <ProtectedRoute redirectUrl="/login" path="/admin/:id" component={Detailpage} orders={this.state.orders} />
            <ProtectedRoute redirectUrl="/login" path="/edit/:id" component={Editpage} changeState={this.changeState} orders={this.state.orders} />
            <ProtectedRoute exact redirectUrl="/login" component={Home} path="/"/>
          </Switch>
        </div>
      </div>
    );
  }
}

export default App;
