import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class OrderListItem extends Component {
    render() {
        return (
            <div>
                <ul className="collection">
                    <li className="collection-item avatar orderItemList">
                        <span className="title"><strong>Naam: </strong>{this.props.thisOrder.name}</span>
                        <p><strong>Tel.: </strong>{this.props.thisOrder.phoneNumber} <br/>
                        <strong>Notes: </strong> {this.props.thisOrder.notes}
                        </p>
                        <Link to={`/admin/${this.props.thisOrder._id}`} 
                        className="secondary-content"><i className="material-icons">info</i></Link> 
                    </li>
                </ul>
            </div>
        )
    }
}
