import React, { Component } from "react";
import OrderListItem from "../components/OrderListItem";
// import axios from 'axios';
import './Admin.css'

export default class Admin extends Component {

  constructor(props) {
    super(props)
    this.state = {
      // orders: this.props.orders,
      searchItems: this.props.orders
    }
  }

  search = (event) => {
    let searchTerm = event.target.value;

    let searchItems = this.props.orders.filter((order) => (
      order.name.indexOf(searchTerm) >= 0 || order.phoneNumber.indexOf(searchTerm) >= 0 
    ));
    this.setState({searchItems: searchItems});
  }

  render() {
    return (
      <div>
        <div>
          <div className="searchDiv">
            <h3>Administratie</h3>
            <div className="inputDiv">
              <input type="text" name="search" placeholder="search" onChange={this.search}/>
            </div>
          </div>
          <div>
            {this.state.searchItems &&
              this.state.searchItems.map(thisOrder => {
                return (
                  <OrderListItem thisOrder={thisOrder} key={thisOrder._id} />
                );
              })}
          </div>
        </div>
      </div>
    );
  }
}
