import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Detailpage.css";

export default class Detailpage extends Component {
  render() {
    const getOrders = id => {
      let found = this.props.orders.find(order => {
        return order._id === id;
      });
      return found;
    };

    const { params } = this.props.match;
    const foundOrder = getOrders(params.id);

    return (
      <div>
        <ul className="collection">
          <li className="collection-item avatar detailDiv">
            <span className="title">
              <strong>Naam: </strong>
              {foundOrder.name}
            </span>
            <p>
              <strong>Tel.: </strong>
              {foundOrder.phoneNumber}
            </p>
            <p>
              <strong>Datum: </strong>
              {new Date(foundOrder.date).getDate() +
                "/" +
                (new Date(foundOrder.date).getMonth() + 1) +
                "/" +
                new Date(foundOrder.date).getFullYear()}
            </p>
            <p>
              <strong>Verhuur: </strong>
            </p>
            <ul>
              {foundOrder.rental.map(thisRental => {
                return <li>{thisRental}</li>;
              })}
            </ul>
            <p>
              <strong>Buffet keuze: </strong>
            </p>
            <ul>
              {foundOrder.meal.map(thisMeal => {
                return <li>{thisMeal}</li>;
              })}
            </ul>
            <p>
              <strong>Aantal pers.: </strong>
              {foundOrder.amount}
            </p>
            <p>
              <strong>Notes: </strong> {foundOrder.notes}
            </p>
            <Link to={`/edit/${foundOrder._id}`} className="secondary-content">
              <i className="material-icons">edit</i>
            </Link>
          </li>
        </ul>
      </div>
    );
  }
}
