import React, { Component } from 'react'
import './Login.css';
import Auth from '../utils/Auth';
const auth = new Auth();

export default class Login extends Component {

    constructor(props){
        super(props)
        this.state = {
            username: "",
            password: ""
        }
        this.handleFormSubmit = this.handleFormSubmit.bind(this);
    }

    handleFormSubmit = (e)=> {
        e.preventDefault();
        auth.login(this.state.username, this.state.password)
            .then(()=> {
                this.setState({error: ""});
                this.props.history.push("/");
            })
            .catch(({response})=> {
                this.setState({error: response.data.message});
            })
    }
    
    handleFormChange = (e)=> {
    this.setState({ 
        [e.target.name]: e.target.value
    })
    }

    render() {
        return (
            <div>
                <div className="login-form">
                    <div className="row"> 
                        <form className="col s12" onSubmit={this.handleFormSubmit}>
                            <div className="flex-container">
                                <div className="input-field">
                                    <label>Gebruikersnaam</label>
                                    <input type="text" name="username"  onChange={this.handleFormChange}/>
                                </div>
                                <div className="input-field">
                                    <label>Wachtwoord</label>
                                    <input type="password" name="password"  onChange={this.handleFormChange}/>
                                    <span className="helper-text" data-error="wrong" data-success="right"></span>
                                </div>
                                <button className="waves-effect waves-light btn" type="submit" value="submit">Login</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}
