import React, { Component } from "react";
// import OrderListItem from './OrderListItem';

export default class WeekScheduleItem extends Component {
  render() {
    return (
      <div>
        <div className="col s12 m6">
          <div className="card blue-grey darken-1">
            <div className="card-content white-text">
              <span className="card-title">
                {new Date(this.props.thisDay).getDate() +
                  "/" +
                  (new Date(this.props.thisDay).getMonth() +1) +
                  "/" +
                  new Date(this.props.thisDay).getFullYear()}
              </span>
              <p>Order 1</p>
              <p>Order 2</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
