import React, { Component } from "react";
import RentalListItem from '../components/RentalListItem'

export default class Rental extends Component {
  render() {
    let eachOrder = this.props.orders.map(thisOrder =>{
      if(thisOrder.rental.length>0){
        return <RentalListItem thisOrder={thisOrder} key={thisOrder._id} />;
      }
    })
    return (
      <div>
        <h3>Verhuur</h3>
        {eachOrder}
        {/* {this.props.orders &&
          this.props.orders.map(thisOrder => {
            if(thisOrder.rental){
              return <RentalListItem thisOrder={thisOrder} key={thisOrder._id} />;
            }
          })} */}
      </div>
    );
  }
}
