import React, { Component } from "react";
import axios from "axios";
import qs from "querystring";
import "./NewOrder.css";

export default class NewOrder extends Component {

  constructor(props) {
    super(props)
    this.formRef = React.createRef();
    this.domain = process.env.REACT_APP_API;
    this.state = {
        name: "",
        phoneNumber: "",
        date: "",
        rental: null,
        meal: null,
        amount: "",
        notes: ""
    }

    this.handleFormSubmit = this.handleFormSubmit.bind(this);
  }

  handleFormChange = (e)=> {
  this.setState({ 
      [e.target.name]: e.target.value
  })
  }

  handleFormSubmit = (e)=> {
  e.preventDefault(); 
  let {name, phoneNumber, date, rental, meal, amount,notes} = this.state;
  axios({
    method: "POST",
    url: "/newOrder",
    baseURL: this.domain,
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    data: qs.stringify({name, phoneNumber, date, rental, meal, amount, notes}),
  })
  .then((response)=> {
      console.log('The order has been added, front-end')
      this.props.changeState(response.data);
      this.props.history.push('/')
  })
  .catch((error)=> {
      console.log(error)
  })
  }

  render() {
    return (
      <div className="formDiv">
        <h3>Nieuwe bestelling</h3>
        <form className="col s12" onSubmit={this.handleFormSubmit}>
          <div className="input-field col s6">
            <label>Naam: </label>
            <input type="text" name="name" onChange={this.handleFormChange} />
          </div>
          <div className="input-field col s6">
            <label>Tel.: </label>
            <input type="text" name="phoneNumber" onChange={this.handleFormChange} />
          </div>
          <div className=" col s6">
            <label>Datum: </label>
            <input type="date" name="date" onChange={this.handleFormChange}/>
          </div>
          <div className="input-field col s6">
            <label>Verhuur: </label>
            <input type="text" name="rental" onChange={this.handleFormChange}/>
          </div>
          <div className="input-field col s6">
            <label>Buffet keuze: </label>
            <input type="text" name="meal" onChange={this.handleFormChange}/>
          </div>
          <div className="input-field col s6">
            <label>Aantal personen: </label>
            <input type="number" name="amount" onChange={this.handleFormChange}/>
          </div>
          <div className="input-field col s6">
            <label>Notes: </label>
            <input type="text" name="notes" onChange={this.handleFormChange} />
          </div>
          <button className="waves-effect waves-light btn" type="submit" value="submit">Opslaan</button>
        </form>
      </div>
    );
  }
}
